import request from './plugins/request';

const get = (params = {}) => {
    return request({
        url: 'country',
        params:params,
        method:'get'
    })
};

const update = (params = {}) => {
    return request({
        url: `country/${params.id}`,
        params:params,
        method:'put'
    })
};

const remove = (params = {}) => {
    return request({
        url: 'country/${params.id}',
        params:params,
        method:'delete'
    })
};

const create = (params = {}) => {
    return request({
        url: 'country',
        params:params,
        method:'post'
    })
};

export default {
    get, update, remove, create
}