<template>
	<el-main class="main">
		<el-row :gutter="24">
			<el-col :span="10">
				<el-form :model="form" label-width="80px" @submit.native.prevent label-position="left">
					<el-form-item label="片名:">
						<el-input v-model="form.name" size="small" class="input-inner"></el-input>
					</el-form-item>
					<el-form-item :label="`导演${index + 1}:`" v-for="(director, index) in form.director">
						<el-input v-model="form.director[index]" size="small" class="input-inner"></el-input>
						<el-button type="danger" size="small" @click.prevent="removeItem('director', index)">删除</el-button>
					</el-form-item>
					<el-form-item :label="`编剧${index + 1}:`" v-for="(authors, index) in form.authors">
						<el-input v-model="form.authors[index]" size="small" class="input-inner"></el-input>
						<el-button type="danger" size="small" @click.prevent="removeItem('authors', index)">删除</el-button>
					</el-form-item>
					<el-form-item :label="`演员${index + 1}:`" v-for="(actors, index) in form.actors">
						<el-input v-model="form.actors[index]" size="small" class="input-inner"></el-input>
						<el-button type="danger" size="small" @click.prevent="removeItem('actors', index)">删除</el-button>
					</el-form-item>
					
					<el-form-item :label="`别名${index + 1}:`" v-for="(aliases, index) in form.aliases">
						<el-input v-model="form.aliases[index]" size="small" class="input-inner"></el-input>
						<el-button type="danger" size="small" @click.prevent="removeItem('aliases', index)">删除</el-button>
					</el-form-item>
					<el-form-item label="剧情梗概:">
						<el-input type="textarea" v-model="form.describe" size="small" class="input-inner"></el-input>
					</el-form-item>
					<el-form-item label="豆瓣评分:">
						<el-input v-model="form.score" size="small" class="input-inner"></el-input>
					</el-form-item>
					<el-form-item label="类型:">
						<el-select v-model="form.types" multiple filterable allow-create size="small" class="input-inner">
							<el-option v-for="item in types" :key="item._id" :label="item.name" :value="item._id"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="上映日期:">
						<el-date-picker v-model="form.release" type="date" placeholder="选择日期" size="small" class="input-inner"></el-date-picker>
					</el-form-item>
					<el-form-item label="制片国家:">
						<el-select v-model="form.countries" multiple filterable allow-create size="small" class="input-inner">
							<el-option v-for="item in countries" :key="item._id" :label="item.name" :value="item._id"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="片长:">
						<el-input v-model="form.duration" size="small" class="input-inner"></el-input>
					</el-form-item>
					<el-form-item label="电影封面:">
						<el-input v-model="form.covers" size="small" class="input-inner"></el-input>
					</el-form-item>
					<img :src="form.covers" alt="电影封面" v-if="form.covers.length" width="200px" style="margin-bottom: 22px;">
					<el-form-item>
						<el-button v-if="!id.length" type="primary" @click="onSubmit" size="small">立即创建</el-button>
						<el-button v-else type="primary" @click="onUpdate" size="small">确认修改</el-button>
						<el-button size="small" @click="$router.back(-1)">取消</el-button>
						<el-button type="success" round size="small" @click="addItem('director')">添加导演</el-button>
						<el-button type="success" round size="small" @click="addItem('actors')">添加演员</el-button>
						<el-button type="success" round size="small" @click="addItem('authors')">添加编剧</el-button>
						<el-button type="success" round size="small" @click="addItem('aliases')">添加别名</el-button>
					</el-form-item>
				</el-form>

			</el-col>
		</el-row>
	</el-main>
</template>

<script>
	import type from '../services/type.js';
	import country from '../services/country.js';
	import movie from '../services/movie.js';
	
	export default {
		name: 'MoviePage',
		props:{
			id:{
				type:String,
				default:''
			}
		},
		data() {
			return {
				form: {
					name: '',
					director: [''], //导演
					actors: [''], //演员
					authors: [''], //编剧
					aliases: [''], //别名
					describe: '', //剧情梗概
					score: '', //豆瓣评分
					types: [], //类型
					countries: [], //制片国家
					release: '', //上映日期
					duration: '', //片长
					covers: ''//电影封面
				},
				types: [],
				countries:[]
			}
		},
		async mounted() {
			this.getTypeList();
			this.getCountryList();
			if(this.id.length){
				this.form = await movie.get({id: this.id});
				// console.log(this.form);
			}
		},
		methods: {
			async onSubmit() {
				const form = this.form, director = form.director, actors = form.actors, authors = form.authors, aliases = form.aliases;
				if(director[0].indexOf("/") != -1) this.form.director = director[0].split(' / ');
				if(actors[0].indexOf("/") != -1) this.form.actors = actors[0].split(' / ');
				if(authors[0].indexOf("/") != -1) this.form.authors = authors[0].split(' / ');
				if(aliases[0].indexOf("/") != -1) this.form.aliases = aliases[0].split(' / ');
				try{
					await movie.create(this.form);
					this.$message.success('创建成功');
					this.$router.back(-1);
				}catch(e){
					this.$message.success(e.error || '创建失败');
				}
			},
			addItem(item) {
				this.form[item].push('');
			},
			removeItem(item, index) {
				const i = Number(index);
				if (i > 0) {
					this.form[item].splice(i, 1)
				}
			},
			async getTypeList() {
				this.types = (await type.get({
					limit: 99
				})).date;
			},
			async getCountryList() {
				this.countries = (await country.get({
					limit: 99
				})).date;
			},
			async onUpdate(){
				await this.$confirm(`确定求修改该项吗?`, '提示');
				this.form.id = this.id
				try {
					await movie.update(this.form);
					this.dialogFormVisible = false
					this.$message.success('修改成功');
					this.$router.back(-1);
				} catch (e) {
					this.$message.error(e.error || '修改失败');
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.input-inner {
		width: calc(100% - 66px);
		margin-right: 10px;
	}
</style>
